<template>
  <div class="home" @mousemove="showCoords($event)">
    <div class="box" id="content" ref="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'page',
  data () {
    return {
      content: null,
      WIDTN_C: document.body.clientWidth / 2,
      HEIGHT_C: document.body.clientHeight / 2
    }
  },
  mounted () {
    this.content = this.$refs.content
  },
  methods: {
    showCoords (evt) {
      this.content.style.setProperty('transform', `rotateY(${(evt.clientX - this.WIDTN_C) / 10000}deg) rotateX(${(this.HEIGHT_C - evt.clientY) / 10000}deg)`)
    }
  }
}
</script>
<style lang="scss">
  .home{
    height: 100%;
    overflow-y:hidden;
    transform-style: preserve-3d;
    perspective: 10px;
    .box{
      height: calc(100% - 120px);
      background: rgba(255,255,255,0.2);
      width: 60%;
      margin: 60px auto;
      color: #ffffff;
      transform: rotateY(0deg) rotateX(0deg)
    }
  }
  .home::-webkit-scrollbar {
    /*滚动条区域的样式*/
    width: 1px;
    height: 1px;
  }
  .home::-webkit-scrollbar-thumb {
    /*滑动块样式*/
    width: 1px;
    height: 1px;
  }
  .home::-webkit-scrollbar-track {
    /*滑动轨道样式*/
    width: 1px;
    height: 1px;
  }
</style>
