<template>
    <div id="app">
      <transition name="component-fade" mode="out-in">
         <router-view/>
      </transition>
    </div>
</template>
<script>
import { L2Dwidget } from 'live2d-widget'
export default {
  name: 'App',
  data () {
    return {
      list: [
        'haruto',
        'hibiki',
        'hijiki',
        'izumi',
        'koharu',
        'miku',
        'ni-j',
        'z16',
        'unitychan',
        'tororo',
        'nito',
        'nipsilon'
      ],
      url: ''
    }
  },
  mounted () {
    this.check()
    L2Dwidget.init({
      model: {
        jsonPath: this.url
      },
      dialog: {
        enable: true,
        hitokoto: true
      }
    })
  },
  methods: {
    check () {
      const value = parseInt(Math.random() * 12)
      this.url = `https://unpkg.com/live2d-widget-model-${this.list[value]}@1.0.5/assets/${this.list[value]}.model.json`
    }
  }
}

</script>
<style lang="scss">
  * {
    margin: 0;
    padding: 0;
  }
  html, body {
    height: 100%;
  }
  #app{
    height: 100%;
    overflow: hidden;
  }
  .component-fade-enter-active, .component-fade-leave-active {
    transition: all .5s ease;
  }
  .component-fade-enter, .component-fade-leave-to{
    opacity: 0;
    transform: scale(3);
  }
</style>
