<template>
  <canvas id="map" class="map" width="800" height="800"></canvas>
</template>

<script>
const Bamboo = function (x, y, isHead) {
  this.x = x
  this.y = y
  this.isHead = isHead
  return this
}
export default {
  name: 'snake',
  data () {
    return {
      config: {
        MeshDensity: 50
      },
      DirectionKey: [],
      canvas: null,
      context: null,
      cellSize: null,
      memoryData: {
        // 身体
        snakeBody: [],
        // 移动方向
        moveDirection: 'right',
        // 敌人
        enemy: {
          x: 0,
          y: 0
        },
        // 分数
        score: 0,
        // 是否死亡
        isDied: false
      },
      setTime: null
    }
  },
  mounted () {
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.setTime)
  },
  methods: {
    init () {
      this.DirectionKey[37] = 'left'
      this.DirectionKey[38] = 'up'
      this.DirectionKey[39] = 'right'
      this.DirectionKey[40] = 'down'
      this.canvas = document.getElementById('map')
      this.context = this.canvas.getContext('2d')
      this.cellSize = this.canvas.width / this.config.MeshDensity
      for (let i = 3; i >= 0; i--) {
        const bamboo = new Bamboo(i, this.config.MeshDensity - 1, false)
        if (i === 3) {
          bamboo.isHead = true
        }
        this.memoryData.snakeBody.push(bamboo)
      }
      this.memoryData.enemy.x = Math.floor(Math.random() * this.config.MeshDensity)
      this.memoryData.enemy.y = Math.floor(Math.random() * this.config.MeshDensity)
      this.snakeMove()
      this.canEat()
      this.isDead()
      this.drawSnakeBody()
      this.drawEnemy()
      this.drawTextScore()
      this.drawTextFailed()
      this.drawFrame()
      this.worldTime()
      this.turnCtl()
      const that = this
      window.addEventListener('keydown', that.turnCtl, false)
    },
    snakeMove () {
      const oldHead = this.memoryData.snakeBody[0]
      oldHead.isHead = false
      const newHead = new Bamboo()
      newHead.isHead = true
      if (this.canEat()) {
        newHead.x = this.memoryData.enemy.x
        newHead.y = this.memoryData.enemy.y
        this.memoryData.enemy.x = Math.floor(Math.random() * this.config.MeshDensity)
        this.memoryData.enemy.y = Math.floor(Math.random() * this.config.MeshDensity)
        this.memoryData.score++
      } else {
        switch (this.memoryData.moveDirection) {
          case 'up':
            newHead.y = oldHead.y - 1
            newHead.x = oldHead.x
            break
          case 'right':
            newHead.y = oldHead.y
            newHead.x = oldHead.x + 1
            break
          case 'down':
            newHead.y = oldHead.y + 1
            newHead.x = oldHead.x
            break
          case 'left':
            newHead.y = oldHead.y
            newHead.x = oldHead.x - 1
            break
        }
        if (newHead.x < 0) {
          newHead.x = this.config.MeshDensity - 1
        }
        if (newHead.x > this.config.MeshDensity - 1) {
          newHead.x = 0
        }
        if (newHead.y < 0) {
          newHead.y = this.config.MeshDensity - 1
        }
        if (newHead.y > this.config.MeshDensity - 1) {
          newHead.y = 0
        }
        this.memoryData.snakeBody.splice(this.memoryData.snakeBody.length - 1, 1)
      }
      this.memoryData.snakeBody.unshift(newHead)
    },
    canEat () {
      const head = this.memoryData.snakeBody[0]
      let result = false
      switch (this.memoryData.moveDirection) {
        case 'up':
          if (head.y === this.memoryData.enemy.y + 1 && head.x === this.memoryData.enemy.x) {
            result = true
          }
          break
        case 'right':
          if (head.y === this.memoryData.enemy.y && head.x === this.memoryData.enemy.x - 1) {
            result = true
          }
          break
        case 'down':
          if (head.y === this.memoryData.enemy.y - 1 && head.x === this.memoryData.enemy.x) {
            result = true
          }
          break
        case 'left':
          if (head.y === this.memoryData.enemy.y && head.x === this.memoryData.enemy.x + 1) {
            result = true
          }
          break
      }
      return result
    },
    isDead () {
      const head = this.memoryData.snakeBody[0]
      let result = false
      for (let i = 1; i < this.memoryData.snakeBody.length; i++) {
        const bamboo = this.memoryData.snakeBody[i]
        if (bamboo) {
          if (head.x === bamboo.x && head.y === bamboo.y) {
            result = true
            break
          }
        }
      }
      return result
    },
    drawSnakeBody () {
      for (let i = 0; i < this.memoryData.snakeBody.length; i++) {
        const bamboo = this.memoryData.snakeBody[i]
        if (bamboo) {
          const x = bamboo.x * this.cellSize
          const y = bamboo.y * this.cellSize
          if (bamboo.isHead) {
            this.context.fillStyle = 'blue'
          } else {
            this.context.fillStyle = 'green'
          }
          this.context.save()
          this.context.fillRect(x, y, this.cellSize, this.cellSize)
        }
      }
      this.context.restore()
    },
    drawEnemy () {
      const x = this.memoryData.enemy.x * this.cellSize
      const y = this.memoryData.enemy.y * this.cellSize
      this.context.fillStyle = 'red'
      this.context.save()
      this.context.fillRect(x, y, this.cellSize, this.cellSize)
      this.context.restore()
    },
    drawTextScore () {
      this.context.fillStyle = 'cyan'
      this.context.save()
      this.context.font = 'italic 30px sans-serif'
      this.context.textBaseline = 'top'
      this.context.fillText(this.memoryData.score, 0, 0)
      this.context.restore()
    },
    drawTextFailed () {
      if (this.memoryData.isDied) {
        this.context.fillStyle = '#000'
        this.context.save()
        this.context.font = 'normal 30px sans-serif'
        this.context.textBaseline = 'top'
        const text = 'YOU DIED'
        this.context.fillText(text, this.config.MeshDensity / 2 * this.cellSize - text.length * this.cellSize / 2, this.config.MeshDensity / 2 * this.cellSize)
        this.context.restore()
      }
    },
    drawFrame () {
      const that = this
      window.requestAnimationFrame(that.drawFrame, that.canvas)
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
      this.drawEnemy()
      this.drawSnakeBody()
      this.drawTextScore()
      this.drawTextFailed()
    },
    worldTime () {
      this.setTime = setInterval(() => {
        this.snakeMove()
        if (this.isDead()) {
          clearInterval(this.setTime)
          this.memoryData.isDied = true
        }
      }, 200)
    },
    turnCtl (e) {
      const event = e || window.Event
      if (!event.keyCode || event.keyCode === 0) {
        event.keyCode = event.which
      }
      const direction = this.DirectionKey[event.keyCode]
      if (direction) {
        switch (this.memoryData.moveDirection) {
          case 'up':
            if (direction === 'down') {
              return false
            }
            break
          case 'right':
            if (direction === 'left') {
              return false
            }
            break
          case 'down':
            if (direction === 'up') {
              return false
            }
            break
          case 'left':
            if (direction === 'right') {
              return false
            }
            break
        }
        this.memoryData.moveDirection = this.DirectionKey[event.keyCode]
      }
    }
  }
}
</script>

<style scoped>
  .map {
    cursor: none;
    border: 1px solid #fff;
  }
</style>
