<template>
  <canvas id="canvas" ref="cvs"></canvas>
</template>

<script>
import bg from '@/assets/bg.jpg'
export default {
  name: 'canvasBg',
  data () {
    return {
      WINDOW_WIDTH: document.body.offsetWidth,
      WINDOW_HEIGHT: document.body.offsetHeight,
      canvas: null,
      context: null,
      num: 400,
      stars: [],
      meteorArr: [],
      pattern: null,
      time: null
    }
  },
  mounted () {
    this.canvas = document.getElementById('canvas')
    this.canvas.width = this.WINDOW_WIDTH = document.body.offsetWidth
    this.canvas.height = this.WINDOW_HEIGHT = document.body.offsetHeight
    this.context = this.canvas.getContext('2d')
    const img = new Image()
    img.src = bg
    img.onload = () => {
      this.pattern = this.context.createPattern(img, 'repeat')
    }
    this.addStar()
    this.addMeteor()
    this.time = setInterval(this.render, 20)
  },
  beforeDestroy () {
    clearInterval(this.time)
  },
  methods: {
    addStar () {
      for (let i = 0; i < this.num; i++) {
        this.stars.push({
          x: Math.round(Math.random() * this.WINDOW_WIDTH),
          y: Math.round(Math.random() * this.WINDOW_HEIGHT),
          r: Math.random() * 8,
          ra: Math.random() * 0.05,
          alpha: Math.random(),
          vx: Math.random() * 0.2 - 0.1,
          vy: Math.random() * 0.2 - 0.1
        })
      }
    },
    addMeteor () {
      for (let i = 0; i < 50; i++) {
        this.meteorArr.push({
          x: Math.random() * this.canvas.width * 20,
          y: -Math.random() * this.canvas.height,
          r: 0.1,
          opacity: 1
        })
      }
    },
    render () {
      this.context.fillStyle = this.pattern
      this.context.fillRect(0, 0, this.WINDOW_WIDTH, this.WINDOW_HEIGHT)
      for (const star of this.stars) {
        star.alpha += star.ra
        if (star.alpha <= 0) {
          star.alpha = 0
          star.ra = -star.ra
          star.vx = Math.random() * 0.2 - 0.1
          star.vy = Math.random() * 0.2 - 0.1
        } else if (star.alpha > 1) {
          star.alpha = 1
          star.ra = -star.ra
        }
        star.x += star.vx
        if (star.x >= this.WINDOW_WIDTH) {
          star.x = 0
        } else if (star.x < 0) {
          star.x = this.WINDOW_WIDTH
          star.vx = Math.random() * 0.2 - 0.1
          star.vy = Math.random() * 0.2 - 0.1
        }
        star.y += star.vy
        if (star.y >= this.WINDOW_HEIGHT) {
          star.y = 0
          star.vy = Math.random() * 0.2 - 0.1
          star.vx = Math.random() * 0.2 - 0.1
        } else if (star.y < 0) {
          star.y = this.WINDOW_HEIGHT
        }
        this.context.beginPath()
        const bg = this.context.createRadialGradient(star.x, star.y, 0, star.x, star.y, star.r)
        bg.addColorStop(0, 'rgba(255,255,255,' + star.alpha + ')')
        bg.addColorStop(1, 'rgba(255,255,255,0)')
        this.context.fillStyle = bg
        this.context.arc(star.x, star.y, star.r, 0, Math.PI * 2, true)
        this.context.fill()
        this.context.closePath()
      }
      for (const meteor of this.meteorArr) {
        let x = meteor.x
        let y = meteor.y
        let r = meteor.r
        for (let j = r; j < 70; j++) {
          this.context.beginPath()
          this.context.fillStyle = 'rgb(255,255,255)'
          this.context.arc(x, y, r, 0, 360 * Math.PI / 180, true)
          r += 0.02
          x -= 2
          y += 1
          this.context.fill()
          this.context.closePath()
        }
        meteor.x -= 16
        meteor.y += 8
        if (meteor.y >= this.canvas.height + 50) {
          meteor.x = Math.random() * this.canvas.width * 20
          meteor.y = -Math.random() * this.canvas.height
        }
      }
    }
  }
}
</script>

<style scoped>
  #canvas {
    position: fixed;
    z-index: -1;
  }
</style>
