<template>
  <div class="main">
    <div id="content">
      <div class="game">
        <el-button size="mini"  @click="back">返回</el-button>
        <el-button size="mini"  name="start" class="start" ref="start" value="开始" @click="start">开始</el-button>
        <span>分数:</span><span>{{score}}</span>
        <span>倒计时:</span><span>{{times}}</span>
      </div>
    </div>
    <canvas ref="myCanvas" id="myCanvas"></canvas>
  </div>
</template>

<script>
function Letter (that) {
  this.x = that.rand(0, that.canvas.width - 25)
  this.y = 0
  this.speedY = that.rand(1, 6)
  this.val = that.letStr[that.rand(0, 25)]
  this.color = ['magenta', 'golden', 'yellow']
}
Letter.prototype.draw = function (that) {
  that.cxt.font = 'Bold 25px verdana'
  that.cxt.fillStyle = this.color[that.rand(0, 2)]
  that.cxt.shadowColor = 'darkblue'
  that.cxt.shadowBlur = 15
  that.cxt.fillText(this.val, this.x, this.y)
}
// 移动的方法
Letter.prototype.move = function () {
  this.y += this.speedY
}
// 删除超除屏幕的对象
Letter.prototype.clear = function (that) {
  return this.y >= that.canvas.height
}
export default {
  name: 'typing',
  data () {
    return {
      startBtn: null,
      times: 30,
      score: null,
      canvas: null,
      cxt: null,
      letStr: null,
      letterArr: [],
      index: 0,
      timer: null,
      game: null
    }
  },
  mounted () {
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.timer)
    clearInterval(this.game)
  },
  methods: {
    init () {
      this.startBtn = this.$refs.start
      this.canvas = this.$refs.myCanvas
      this.cxt = this.canvas.getContext('2d')
      this.canvas.width = document.documentElement.clientWidth
      this.canvas.height = document.documentElement.clientHeight
      this.letStr = 'abcdefghijklmnopqrstuvwsyz'
    },
    // 随机数
    rand (min, max) {
      return parseInt(Math.random() * (max - min + 1) + min)
    },
    start () {
      clearInterval(this.timer)
      clearInterval(this.game)
      this.game = setInterval(() => {
        this.cxt.clearRect(0, 0, this.canvas.width, this.canvas.height)
        // 先创建对象
        if (this.index % 20 === 0) {
          this.index++
          const letter = new Letter(this)
          this.letterArr.push(letter)
        }
        for (let i = 0; i < this.letterArr.length; i++) {
          this.letterArr[i].move(this)
          if (this.letterArr[i].clear(this)) {
            this.letterArr.splice(i, 1)
            i--
          } else {
            this.letterArr[i].draw(this)
          }
        }
        this.index++
      }, 30)
      // 倒计时

      this.timer = setInterval(() => {
        this.times = this.times - 0 - 1
        if (this.times === 0) {
          clearInterval(this.timer)
          clearInterval(this.game)
          alert('分数是:' + this.score)
          location.reload()
        }
      }, 1000)
      document.onkeypress = (ev) => {
        const evObj = ev || window.Event
        // 将对应的ASC转成字符
        // const str = String.fromCharCode(evObj.keyCode)
        const str = evObj.key
        for (let i = 0; i < this.letterArr.length; i++) {
          if (this.letterArr[i].val === str) {
            this.letterArr.splice(i, 1)
            i--
            this.score = this.score - 0 + 1
            break
          }
        }
      }
    },
    back () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
  body {
    background-color: #333;
  }

  .main{
    background: #333;
    overflow: hidden;
    height: 100%;
    box-sizing: border-box;
    padding: 30px;
    z-index: -1000;
  }
  #content {
    position: absolute;
    width: 500px;
    z-index: 1000;
  }
  #myCanvas{
    position: fixed;
    left: 0;
    top: 0;
  }
  .game {
    position: absolute;
    left: 0;
    top: 0;
    color: white;
  }
  .el-button{
    margin-right: 30px;
  }
</style>
