<template>
  <div class="main">
    <canvas id="canvas"></canvas>
    <el-button class="button" size="mini" @click="back">返回</el-button>
  </div>
</template>

<script>
export default {
  name: 'blueRain',
  data () {
    return {
      WINDOW_WIDTH: document.body.clientWidth,
      WINDOW_HEIGHT: document.body.clientHeight,
      rains: [],
      timer: null
    }
  },
  mounted () {
    this.WINDOW_WIDTH = document.body.clientWidth
    this.WINDOW_HEIGHT = document.body.clientHeight
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    init () {
      const canvas = document.getElementById('canvas')
      const context = canvas.getContext('2d')
      canvas.width = this.WINDOW_WIDTH
      canvas.height = this.WINDOW_HEIGHT
      context.beginPath()
      context.fillStyle = 'rgb(0,5,5)'
      context.fillRect(0, 0, this.WINDOW_WIDTH, this.WINDOW_HEIGHT)
      context.closePath()
      this.updateRain(context)
    },
    updateRain (context) {
      // const x = 100
      // const y = 0
      // const R = 0
      // const border = 0 // 0-1
      // const arpha = 1
      // setInterval(function(){
      //         addRains();
      // },300);
      for (let i = 0; i < 50; i++) {
        this.addRains()
      }
      this.timer = setInterval(() => {
        context.clearRect(0, 0, this.WINDOW_WIDTH, this.WINDOW_HEIGHT)
        context.beginPath()
        context.fillStyle = 'rgb(0,5,5)'
        context.fillRect(0, 0, this.WINDOW_WIDTH, this.WINDOW_HEIGHT)
        this.rain(context)
      }, 16)
    },

    // 绘制雨滴
    rain (context) {
      for (let i = 0; i < this.rains.length; i++) {
        let _y = this.rains[i].y
        let r = 2
        let arpha = 1
        for (let j = 0; j < 50; j++) {
          if (_y <= this.rains[i].surface) {
            context.beginPath()
            context.arc(this.rains[i].x, _y, r, 0, Math.PI * 2, true)
            context.fillStyle = 'rgba(0,223,223,' + arpha + ')'
            context.fill()
            context.closePath()
          }
          _y -= r * 1.5
          r -= 0.04
          arpha -= 0.02
        }
        if (this.rains[i].y > this.rains[i].surface) {
          if (this.rains[i].waveHr <= 1) {
            this.wave(context, i)
            this.rains[i].waveR += 0.5
            this.rains[i].waveHr += 0.005
            if (this.rains[i].waveArpha <= 0.5) {
              this.rains[i].waveArpha = 0.5
            } else {
              this.rains[i].waveArpha -= 0.001
            }
          } else {
            // rains.splice(i,1);
            this.rains[i] = {
              x: parseInt(Math.random() * this.WINDOW_WIDTH),
              y: -parseInt(Math.random() * 500),
              v: parseInt(Math.random() * 6 + 4),
              rainR: 2,
              rainArpha: 1,
              waveHr: 0,
              waveArpha: 1,
              waveR: 1,
              surface: this.WINDOW_HEIGHT - parseInt(50 + Math.random() * 100)
            }
          }
        }
        this.rains[i].y += this.rains[i].v
      }
    },
    // 绘制波纹
    wave (context, num) {
      context.save()
      context.scale(1, 0.5)
      context.beginPath()
      const g = context.createRadialGradient(this.rains[num].x, (this.rains[num].surface) * 2, 0, this.rains[num].x, (this.rains[num].surface) * 2, this.rains[num].waveR)
      g.addColorStop(0, 'rgba(0,223,223,0)')
      g.addColorStop(this.rains[num].waveHr, 'rgba(0,223,223,0)')
      g.addColorStop(1, 'rgba(0,223,223,' + this.rains[num].waveArpha + ')')
      context.fillStyle = g
      context.arc(this.rains[num].x, (this.rains[num].surface) * 2, this.rains[num].waveR, 0, Math.PI * 2)
      context.fill()
      context.closePath()
      context.restore()
    },

    addRains () {
      const aRain = {
        x: parseInt(Math.random() * this.WINDOW_WIDTH),
        y: -parseInt(Math.random() * 1000),
        v: parseInt(Math.random() * 6 + 4),
        rainR: 2,
        rainArpha: 1,
        waveHr: 0,
        waveArpha: 1,
        waveR: 1,
        surface: this.WINDOW_HEIGHT - parseInt(50 + Math.random() * 100)
      }
      this.rains.push(aRain)
    },
    back () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
  #canvas {
    position: fixed;
    z-index: -1;
  }
  .button{
    position: fixed;
    bottom: 30px;
    left: 30px;
  }
</style>
