import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import meteorView from '../views/meteorView'
import typingView from '../views/typingView'
import blueRainView from '../views/blueRainView'
import snakeView from '../views/snakeView'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/imgPixelView',
    name: 'imgPixelView',
    component: () => import(/* webpackChunkName: "about" */ '../views/imgPixelView.vue')
  },
  {
    path: '/meteorView',
    name: 'meteorView',
    component: meteorView
  },
  {
    path: '/typingView',
    name: 'typingView',
    component: typingView
  },
  {
    path: '/blueRainView',
    name: 'blueRainView',
    component: blueRainView
  },
  {
    path: '/snakeView',
    name: 'snakeView',
    component: snakeView
  }
]

const router = new VueRouter({
  routes
})

export default router
